import React, { useEffect, useState } from 'react'
// import ReactDataGrid from 'react-data-grid'
import useFoodWasteTable from '../../hooks/useFoodWasteTable'
import { getWeekDateRange } from '../../utils/date';
import Toast from '../../components/Toast';
import Button from '../../components/Button';
// import DataGridWrapper from '../../components/DataGridWrapper';



function FoodWaste() {
    const {
        week,
        setWeek,
        filteredRecords,
        handleUpdate,
        saveUpdates,
        loading,
        isSaving,
        hasPendingUpdates,
        toast,
        dismissToast
    } = useFoodWasteTable();

    const [isClient, setIsClient] = useState(false);

    useEffect(() => {
        setIsClient(true);
    }, []);

    const rows = filteredRecords.map((item) => ({
        id: item.id,
        weekDay: new Date(item.date).toLocaleDateString("en-GB", {
            weekday: "long",
            day: "2-digit",
            month: "2-digit",
            year: "numeric"
        }),
        numberOfUsers: item.number_of_users,
        plateWaste: item.food_waste,
        totalWaste: item.total_waste,
    }));

    const columns = [
        { key: "weekDay", name: "Week Day" },
        { key: "numberOfUsers", name: "Users", editable: true },
        { key: "plateWaste", name: "Plate Waste", editable: true },
        { key: "totalWaste", name: "Total Waste", editable: true },
    ];

    const { start, end } = getWeekDateRange(week);

    return (
        <div className="p-6">
            <div className="flex justify-between gap-4 mb-4">
                <div className="flex gap-4 items-center">
                    <h3 className="font-semibold">
                        <span className='text-[var(--primary-color)] text-2xl'>Week {week} /</span> {start} - {end}
                    </h3>
                    <div className="flex gap-2">
                        <button
                            onClick={() => setWeek((prev) => Math.max(prev - 1, 1))}
                            className="px-3 py-1 border rounded hover:bg-gray-100"
                        >
                            ←
                        </button>

                        <button
                            onClick={() => setWeek((prev) => prev + 1)}
                            className="px-3 py-1 border rounded hover:bg-gray-100"
                        >
                            →
                        </button>
                    </div>
                </div>


                <div className="flex justify-end items-center mb-6 pos-top">
                    <p className="mr-4">Remember to update before leaving</p>
                    <Button onClick={saveUpdates} variant="primary" disabled={isSaving}>
                        Updates
                    </Button>
                </div>
            </div>

            {toast && (
                <Toast
                    message={toast.message}
                    type={toast.type}
                    onClose={dismissToast}
                />
            )}

            {loading ? (
                <p>Loading…</p>
            ) : (
                <table className="w-full border-collapse border border-gray-300 text-sm">
                    <thead className="bg-gray-100">
                        <tr>
                            <th className="border p-2 text-left">Week Day</th>
                            <th className="border p-2 text-left">Users</th>
                            <th className="border p-2 text-left">Plate Waste (g)</th>
                            <th className="border p-2 text-left">Total Waste (g)</th>
                        </tr>
                    </thead>

                    <tbody>
                        
                    </tbody>
                </table>
            )}
        </div>
    );

}

export default FoodWaste;
